$(function() {
    const $sliderPrograms = $(".js-programs-slider");
    const $sliderProgramsPrev = $(".js-programs-slider-prev");
    const $sliderProgramsNext = $(".js-programs-slider-next");


    if(!$sliderPrograms.length) return;

    $sliderPrograms.slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        dots: false,
        arrows: false,

        responsive: [
            {
                breakpoint: 1241,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: false,
                    arrows: false,
                }

            },

            {
                breakpoint: 1025,
                settings: {
                    slidesToShow: 1.3,
                    slidesToScroll: 1,
                    dots: true,
                    arrows: false,
                }

            },

            {
                breakpoint: 601,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    dots: true,
                    arrows: false,
                }

            },
        ]
    });


    
    $sliderProgramsPrev.on('click', ()=> $sliderPrograms.slick('slickPrev'));
    $sliderProgramsNext.on('click', ()=> $sliderPrograms.slick('slickNext'));
})