$(function() {
    const $select = $('.js-select');
    if (!$select.length) return;

    $select.each((_, select) => {
        const $that = $(select);
        const { placeholder, type, label } =$that.data()

        $that.select2({
            dropdownAutoWidth: true,
            placeholder: placeholder,
            width: null,
            dropdownCssClass: type === 'horizontal' ? 'dd-horizontal' : type === 'vertical' ? 'dd-vertical' : '',
            dropdownParent: $that.parent()
        })

        const getDdList =()=> {
            const ddSelector = type === 'horizontal' ? '.select2-dropdown.dd-horizontal' : type === 'vertical' ? '.select2-dropdown.dd-vertical' : ''
            const ddList = $(ddSelector)
            ddList.find('.js-select2-dropdown-label').remove()
            ddList.parent().addClass('dd-list')
            return ddList
        }

        $that.on('select2:open', ()=> {
            const ddList = getDdList()

            if (ddList.length) {
                ddList.find('.select2-results').before($(`<div class="js-select2-dropdown-label select2-dropdown-label">${label}</div>`))
            }
        })
    })
})